import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import formatDistance from 'date-fns/formatDistance'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import ListSubheader from '@material-ui/core/ListSubheader'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'

import DeleteIcon from '@material-ui/icons/Delete'

const styles = theme => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  button: {
    margin: '1rem',
    width: 'calc(100% - 2rem)'
  }
})

class UploadContent extends Component {
  render () {
    const {
      classes,
      comicList,
      isDataLoaded,
      addNewComic,
      deleteComicItem,
      deleteLocalComicItem,
      setDataLoaded
    } = this.props

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            {
              !isDataLoaded &&
              <div className={classes.loader}>
                <CircularProgress size={80} />
              </div>
            }
            {
              isDataLoaded &&
              <div>
                <Button
                  variant='contained'
                  color='primary'
                  className={classes.button}
                  onClick={() => {
                    setDataLoaded(false)
                    addNewComic()
                  }}>
                  ADD MOCK COMIC
                </Button>
                <List
                  component='nav'
                  subheader={<ListSubheader component='div'>Comic List</ListSubheader>}>
                  {
                    comicList.map((comic, index) => {
                      const {title, timestamp, isLoading} = comic
                      return (
                        <ListItem key={index} divider={index !== (comicList.length - 1)}>
                          <ListItemText
                            primary={title}
                            secondary={`Create Time : ${formatDistance(timestamp, new Date())}`} />
                          <ListItemSecondaryAction>
                            <IconButton aria-label='delete' onClick={() => {
                              deleteComicItem(comic)
                              deleteLocalComicItem(comic)
                            }}>
                              {isLoading ? <CircularProgress size={30} /> : <DeleteIcon />}
                            </IconButton>
                          </ListItemSecondaryAction>
                        </ListItem>
                      )
                    })
                  }
                </List>
              </div>
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(UploadContent)



