import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import UploadContent from '../component/Upload/UploadContent'

class Upload extends Component {
  componentDidMount () {
    const {setDataLoaded, getComicList} = this.props

    setDataLoaded(false)
    getComicList()
  }

  render () {
    return (
      <main>
        <UploadContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    comicList: state.comicList,
    isDataLoaded: state.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    addNewComic: (comic) => dispatch(actionCreators.addNewComic(comic)),
    getComicList: () => dispatch(actionCreators.getComicList()),
    deleteComicItem: (comic) => dispatch(actionCreators.deleteComicItem(comic)),
    deleteLocalComicItem: (comic) => dispatch(actionCreators.deleteLocalComicItem(comic)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Upload)



